.footer {
    display: grid;
    background-color: #fff;
    width: 100%;
    height: 100%;
    align-items: center;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.55);
    grid-template-columns: repeat(3, max-content);
    grid-column-gap: 10px;
}

.icon {
    width: 25px;
    height: 25px;
}

.link {
    padding-left: 15px;
}